import React from "react"
import { graphql } from "gatsby"

import Layout from "../components/layout"
import SEO from "../components/seo"
import { Translation } from "react-i18next"
import i18n from "../lib/i18n"

import HeroBuilders from "../components/heros/hero-builders"
import GetInTouchBuilders from "../components/forms/contact-builders"


export const query = graphql`
  query {
    wpHero(id: { eq: "cG9zdDo4MDE=" }) {
      id
      title
      heroFields {
        en {
          headline
          smallcta
          subheadline
          body
          buttontext
          buttonlink
          image {
            id
            sourceUrl
          }
        }
        es {
          headline
          smallcta
          subheadline
          body
          buttontext
          buttonlink
          image {
            id
            sourceUrl
          }
        }
      }
    }
  }
`

const BuildersPage = ({ data }) => {
  const hero = data.wpHero.heroFields
  return (
    <Layout>
      <Translation>
        {t => (
          <>
            {i18n.language === "en" ? (
              <SEO title="Texas HVAC Company | Residential &amp; Commercial HVAC Contractors" />
            ) : (
              <SEO title="Compañía de HVAC en Texas | Contratadores de HVAC Residencial y Comercial" />
            )}
            <HeroBuilders />
          </>
        )}
      </Translation>
      <GetInTouchBuilders
        locationName="Air Rey A/C &amp; Heating"
        // address="5504 Bandera Rd. Suite 202"
        city="Serving Austin, Houston, Dallas, &amp; San Antonio"
        state=""
        zip=""
        phone=""
        email="builders@airreydfw.com"
      />
    </Layout>
  )
}

export default BuildersPage
